const FontType = {
  0: 'Sans-serif',
  1: 'Microsoft Yahei',
  2: 'SimHei',
  3: 'SimSun',
  4: 'KaiTi',
  5: 'FangSong'
};

const BatchLayer = {
  batchVector: ({ name, code, parentId, sort }) => {
    let layerBorder = Default.vectorParams();
    layerBorder = {
      ...layerBorder,
      ...{
        name: name,
        layerId: '00000000-0000-0000-0000-000000000000',
        borderSize: 3,
        borderColor: Tool.randomColor(),
        borderType: 1,
        isGround: 2,
        remark: name
      }
    };
    return {
      id: '00000000-0000-0000-0000-000000000000',
      name: name,
      type: LayerType.VectorCollection,
      check: 1,
      code: code,
      sort: sort,
      canDelete: true,
      checkbox: true,
      children: [],
      layerViews: [],
      isBatch: 1,
      parentId: parentId,
      layerBorder: layerBorder,
      layerText: null,
      layerLabel: null
    };
  },
  batchLabel: ({ name, code, parentId, sort, content = '' }) => {
    let layerText = Default.labelParams();
    layerText = {
      ...layerText,
      ...{
        content: content,
        layerId: '00000000-0000-0000-0000-000000000000',
        isGround: 2,
        remark: content
      }
    };
    return {
      id: '00000000-0000-0000-0000-000000000000',
      name: name,
      type: LayerType.LabelCollection,
      check: 1,
      code: code,
      sort: sort,
      canDelete: true,
      checkbox: true,
      children: [],
      layerViews: [],
      isBatch: 1,
      parentId: parentId,
      layerText: layerText
    };
  },
  batchWater: ({ name, code, parentId, sort }) => {
    let layerWaterSurface = Default.waterParams();
    layerWaterSurface = {
      ...layerWaterSurface,
      ...{
        name: name,
        layerId: '00000000-0000-0000-0000-000000000000',
        borderSize: 0,
        borderColor: Tool.randomColor(),
        isGround: 2,
        remark: name
      }
    };
    return {
      id: '00000000-0000-0000-0000-000000000000',
      name: name,
      type: LayerType.WaterCollection,
      check: 1,
      code: code,
      sort: sort,
      canDelete: true,
      checkbox: true,
      children: [],
      layerViews: [],
      isBatch: 1,
      parentId: parentId,
      layerWaterSurface: layerWaterSurface
    };
  },
  batchModel: ({ name, code, parentId, sort, url }) => {
    let layerModel = Default.modelParams();
    layerModel = {
      ...layerModel,
      ...{
        name: name,
        layerId: '00000000-0000-0000-0000-000000000000',
        isGround: 2,
        url: url
      }
    };
    return {
      id: '00000000-0000-0000-0000-000000000000',
      name: name,
      type: LayerType.ModelCollection,
      check: 1,
      code: code,
      sort: sort,
      canDelete: true,
      checkbox: true,
      children: [],
      layerViews: [],
      isBatch: 1,
      parentId: parentId,
      layerModel: layerModel
    };
  },
  batchBillboard: ({ name, code, parentId, sort, labelId }) => {
    let layerLabel = Default.billboardParams();
    layerLabel = {
      ...layerLabel,
      ...{
        name: name,
        layerId: '00000000-0000-0000-0000-000000000000',
        isGround: 2,
        labelId: labelId
      }
    };
    return {
      id: '00000000-0000-0000-0000-000000000000',
      name: name,
      type: LayerType.BillboardCollection,
      check: 1,
      code: code,
      sort: sort,
      canDelete: true,
      checkbox: true,
      children: [],
      layerViews: [],
      isBatch: 1,
      parentId: parentId,
      layerLabel: layerLabel
    };
  }
};

export { LayerType, Default, FontType, BatchLayer };
